.history__container { max-width: 1490px; }

.history_default { padding: 70px 0 160px; }

.history-timeline { margin: 27px 0 0; padding: 0 175px; display: none; background: url(../img/bg/dashed-line_bg.png) no-repeat 120% 10%; }

.history-timeline.slick-initialized { display: block; }

.history-timeline__slide { background: url(../img/bg/dashed-line.png) no-repeat 26% 11%; }

.history-timeline__slide:nth-child(2n) { background: url(../img/bg/dashed-line.png) no-repeat 86.2% 13%; }

.history-timeline__slide:nth-child(11) { background: url(../img/bg/dashed-line.png) no-repeat 85.3% 13%; }

.history-timeline__item { width: 260px; float: left; }

.history-timeline__item + .history-timeline__item { margin: 60px 0 0 20px; }

.history-timeline__item-year { position: relative; display: inline-block; vertical-align: top; padding: 0 19px; min-width: 122px; height: 36px; line-height: 36px; font-size: 18px; color: #fff; font-weight: 700; background: #f7f7f7 url("../img/bg/label.svg") no-repeat 50% 50%/cover; }

.history-timeline__item-text { height: 92px; margin: 10px 0 0; font-size: 15px; line-height: 23px; }

.history-timeline__nav { position: relative; display: flex; justify-content: space-between; max-width: 730px; margin: 40px auto 0; height: 6px; border-radius: 3px; background-color: #cbcdce; font-size: 0; }

.history-timeline__nav li { position: relative; margin-top: -7px; width: 20px; height: 20px; border: 2px solid transparent; border-radius: 50%; background-color: transparent; cursor: pointer; pointer-events: none; transform: translate3d(0, 0, 0); }

.history-timeline__nav li::before { left: 7px; }

.history-timeline__nav li::after { display: none; }

.history-timeline__nav li.slick-active { background-color: #de4e4e; border-color: #fff; animation: myFlip 1.2s; }

.history-timeline__nav li:first-child { margin-left: -10px; }

.history-timeline__nav li:first-child span::before, .history-timeline__nav li:first-child button::after { display: none; }

.history-timeline__nav li:last-child { margin-right: -10px; }

.history-timeline__nav li:last-child span::after, .history-timeline__nav li:last-child button::before { display: none; }

.history-timeline__nav button { pointer-events: all; position: absolute; left: -7px; bottom: -30px; font-size: 14px; font-weight: 700; color: #000; background: transparent; }

.history-timeline__nav button::before { left: 44px; top: -11px; bottom: -14px; }

.history-timeline__nav button::after { top: -11px; left: -13px; bottom: -14px; }

.history-timeline__nav button span::before { left: -42px; bottom: 17px; }

.history-timeline__nav button span::after { right: -44px; bottom: 17px; }

.history-timeline__nav span::before, .history-timeline__nav span::after, .history-timeline__nav button::before, .history-timeline__nav button::after, .history-timeline__nav li::before, .history-timeline__nav li::after { content: ''; position: absolute; background: #cccecf; width: 1px; height: 10px; bottom: -13px; }

@keyframes myFlip { from { transform: perspective(400px) rotate3d(0, 1, 0, 120deg);
    animation-timing-function: ease-in-out;
    opacity: .5; }
  40% { transform: perspective(400px) rotate3d(0, 1, 0, -40deg);
    animation-timing-function: ease-in-out; }
  60% { transform: perspective(400px) rotate3d(0, 1, 0, 20deg);
    opacity: 1; }
  80% { transform: perspective(400px) rotate3d(0, 1, 0, -5deg); }
  to { transform: perspective(400px); } }

