.benefits {
	&__list {
		margin: 58px 0 45px;
		font-size: 0;
		text-align: center;
		@include sm {
			margin: 40px 0; }
		@include xs {
			margin: 25px 0; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 0 20px 45px;
		text-align: center;
		width: 33.33333%;
		@include sm {
			padding: 0 10px 20px; }
		@include xs {
			width: 50%; }
		@include r(599) {
			width: 100%;
			padding: 0 10px 10px; }
		&-pic {
			height: 90px;
			@include vertical;
			@include sm {
				margin-bottom: 4px; }
			&::after {
				content: '';
				display: inline-block;
				vertical-align: middle; }
			&_tape::after {
				margin-bottom: 5px;
				@include s('tape'); }
			&_peoples::after {
				@include s('peoples'); }
			&_building::after {
				@include s('building'); }
			&_vehicle::after {
				margin-bottom: 10px;
				vertical-align: bottom;
				@include s('vehicle'); }
			&_handshake::after {
				margin-bottom: 5px;
				@include s('handshake'); }
			&_pc-shield::after {
				@include s('pc-shield'); }
			&_marker::after {
				@include s('marker'); }
			&_pen-ruler::after {
				@include s('pen-ruler'); }
			&_present::after {
				@include s('present'); }
			&_list::after {
				@include s('list'); }
			&_box::after {
				@include s('box'); }
			&_push-btn::after {
				margin-bottom: 5px;
				@include s('push-btn'); }
			&_cards::after {
				@include s('cards'); }
			&_equipment::after {
				@include s('equipment'); } }
		&-text {
			margin: 5px 0 0; }

		&_horizontal {
			text-align: left;
			@include clr; }
		&_horizontal &-pic {
			float: left;
			width: 86px;
			margin-right: 14px; }
		&_horizontal &-text {
			float: left;
			width: calc(100% - 100px); }

		&-title {
			margin-bottom: 5px;
			font-size: 22px;
			font-weight: 700;
			line-height: 1;
			color: $black;
			@include sm {
				font-size: 19px; } }
		&-sub-title {
			margin-bottom: 5px;
			font-size: 16px;
			font-weight: 500;
			color: $grey; }
		&-descr {
			font-size: 16px;
			color: $grey; } }
	&__slider {
		&-item {
			height: 132px; }
		.owl-carousel__btn {
			@include vertical;
			&_prev {
				left: -40px; }
			&_next {
				right: -40px; }
			&-icon {
				display: inline-block;
				vertical-align: middle;
				stroke: $grey;
				fill: none;
				font-size: 44px; } } }

	&_default {
		padding: 65px 0 45px;
		@include sm {
			padding: 50px 0 10px; } }
	&_souvenirs {
		padding: 65px 0 63px; }
	&_big {
		padding: 65px 0 125px; } }
